@import url(//db.onlinewebfonts.com/c/1725aaa429e4045ba648a1aa73481440?family=FolioW01-Light);
.store-container {
  display: flex;
  justify-content: center; }
  .store-container .item-container {
    max-width: 900px;
    display: flex;
    flex-flow: row wrap;
    justify-content: center; }
    .store-container .item-container .item-card {
      width: 200px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin: 5px; }
      .store-container .item-container .item-card img {
        width: 200px; }
      .store-container .item-container .item-card .item-content {
        display: flex;
        align-items: center;
        justify-content: space-around; }

.home-container {
  height: 100vh;
  background-color: #F4F4F4; }
  .home-container h1 {
    color: #2c2c2c;
    padding: 100px 50px 0;
    font-size: 3rem; }

.shape {
  height: 70vh;
  width: 100%;
  background-image: -webkit-linear-gradient(283deg, #dfdfdf 70%, #F4F4F4 calc(70% + 2px));
  background-image: linear-gradient(167deg, #dfdfdf 70%, #F4F4F4 calc(70% + 2px));
  display: flex;
  align-items: center;
  flex-direction: column; }

body {
  padding: 0;
  margin: 0;
  font-family: "FolioW01-Light"; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

p {
  font-size: 12px; }

